<template>
  <div class="goodsinfo-container">
    <!--  -->
    <transition @before-enter="beforEenter" @enter="enter" @after-enter="afterEnter">
      <div class="ball" v-show="ballFlag" ref="ball"></div>
    </transition>
    <!--  -->
    <div class="mui-card">
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <piclist :lunbotuLIst="picimg" :isfull="false"></piclist>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="mui-card">
      <div class="mui-card-header">{{ goodsinfo.title }}</div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p class="price">
            :<del>{{ goodsinfo.market_price }}</del>&nbsp;&nbsp;&nbsp;:<span class="my-price">{{
              goodsinfo.sell_price }}</span>
          </p>
          <p><boxnumber @getcount="getSelectedCount" :max="goodsinfo.stock_quantity"></boxnumber>
          </p>
          <p>
            <mt-button type="primary" size="small"></mt-button>
            <mt-button type="danger" size="small" @click="addtoshopCar"></mt-button>
          </p>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="mui-card">
      <div class="mui-card-header"></div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p>{{ goodsinfo.goods_no }} </p>
          <p>{{ goodsinfo.stock_quantity }}</p>
          <p>{{ goodsinfo.add_time | dateFormat }} </p>
        </div>
      </div>
      <div class="mui-card-footer">
        <mt-button type="primary" size="large" plain @click="goDesc(id)"></mt-button>
        <mt-button type="danger" size="large" plain @click="goComment(id)"></mt-button>
      </div>
    </div>

  </div>
</template>
<script>
  import piclist from '../common/swipe'
  import boxnumber from '../common/number.vue'
  export default {
    data() {
      return {
        id: this.$route.params.id,
        picimg: [], //
        goodsinfo: {}, // 
        ballFlag: false, // 
        selectCount: 1 //
      }
    },
    created() {
      this.getlunbotu()
      this.getgoodinfo()
      // test
    },
    methods: {
      // 
      getlunbotu() {
        this.$http.get('api/getthumimages/' + this.id).then(res => {
          if (res.body.status === 0) {
            this.picimg = res.body.message
          }
        })
      },
      // 
      getgoodinfo() {
        this.$http.get('api/goods/getinfo/' + this.id)
          .then(res => {
            if (res.body.status === 0) {
              this.goodsinfo = res.body.message[0]
            }
          })
      },
      // 
      goDesc(id) {
        this.$router.push({
          name: 'goodinfo',
          params: {
            id
          }
        })
      },
      //  
      goComment(id) {
        this.$router.push({
          name: 'goodcomment',
          params: {
            id
          }
        })
      },
      // 
      addtoshopCar() {
        this.ballFlag = !this.ballFlag
        //{id: id, count: , price: , selected: }
        let datacar = {
          id: this.id,
          count: this.selectCount,
          price: this.goodsinfo.sell_price,
          selected: true
        }
        this.$store.commit('addTocar',datacar)
      },
      //  
      beforEenter(el) {
        el.style.transform = 'translate(0, 0)'
      },
      enter(el, done) {
        el.offsetWidth
        const ballposition = this.$refs.ball.getBoundingClientRect()
        const badgepostion = document.getElementById('badge').getBoundingClientRect()
        const x = badgepostion.left - ballposition.left
        const y = badgepostion.top - ballposition.top
        el.style.transform = `translate(${x}px, ${y}px)`
        el.style.transition = 'all .5s ease'
        done()
      },
      afterEnter(el) {
        this.ballFlag = !this.ballFlag
      },
      getSelectedCount(count) {
        this.selectCount = count
      }
    },
    components: {
      piclist,
      boxnumber
    }
  }
</script>
<style lang="scss">
  .goodsinfo-container {
    background: #eee;
    overflow: hidden;

    .my-price {
      color: red;
      font-size: 16px;
    }

    .mui-card-footer {
      display: block;

      button {
        margin: 15px 0;
      }
    }

    .ball {
      position: absolute;
      width: 15px;
      height: 15px;
      top: 408px;
      left: 145px;
      background: red;
      border-radius: 50%;
      z-index: 99;
    }

  }
</style>