//  
import Vue from 'vue'
//  App
import App from './App.vue'
//  
import VueRouter from 'vue-router'
//   vue-resource
import VueResource from 'vue-resource'
//  mint-ui
import MintUi from 'mint-ui'
//  mint-uicss
import 'mint-ui/lib/style.css'
//  router 
import router from './router'
//  vuex 
import store from './store'
//   MUI 
import './lib/mui/css/mui.css'
//   MUI 
import './lib/mui/css/icons-extra.css'
//  vue-preview
import VuePreview from 'vue-preview'
//  mint-ui
Vue.use(MintUi)
//  
Vue.use(VueRouter)
//   vue-resource
Vue.use(VueResource)
//  Vue-Preview
Vue.use(VuePreview)
//  
Vue.http.options.root = 'http://www.liulongbin.top:3005';

//  
Vue.filter('dateFormat', function (dataStr) {
  const year = new Date(dataStr),
    yy = year.getFullYear(),
    month = (year.getMonth() + 1).toString().padStart(2, 0),
    dd = (year.getDate() + 1).toString().padStart(2, 0),
    hh = year.getHours().toString().padStart(2, 0),
    mm = year.getMinutes().toString().padStart(2, 0),
    ss = year.getSeconds().toString().padStart(2, 0)
  return `${yy}-${month}-${dd} ${hh}:${mm}:${ss}`
})

var vm = new Vue({
  el: '#app',
  render: h => h(App),
  router, // 
  store
})