<template>
  <div class="cmt-container">
    <h3></h3>
    <hr>
    <textarea placeholder="(120)" maxlength="120" v-model="msg"></textarea>
    <mt-button type="primary" size="large" @click="setComments"></mt-button>
    <div class="cmt-list">
      <div class="cmt-item" v-for="(item, index) in comments" :key="index">
        <div class="cmt-title">
          {{ index + 1 }}
          &nbsp;&nbsp; {{ item.user_name }}
          &nbsp;&nbsp;{{ item.add_time | dateFormat}}
        </div>
        <div class="cmt-body">
          {{item.content == 'undefined'|| item.content == ''?'':item.content}}
        </div>
      </div>
    </div>
    <mt-button type="danger" size="large" plain @click="getMore">{{main}}</mt-button>
  </div>
</template>
<script>
  import {
    Toast
  } from 'mint-ui'
  export default {
    data() {
      return {
        // id: this.$route.params.id,
        page: 1,
        comments: [],
        main: '',
        msg: ''
      }
    },
    created() {
      this.getComments()
    },
    methods: {
      getComments() {
        this.$http
          .get('api/getcomments/' + this.id + '?pageindex=' + this.page)
          .then(res => {
            if (res.body.status === 0) {
              this.comments = this.comments.concat(res.body.message)
              if (res.body.message.length < 10) {
                this.main = ''
                return false
              }
            }

          })
      },
      getMore() {
        this.page++;
        this.getComments()
      },
      setComments() {
        // 
        if (this.msg.trim().length === 0) {
          return Toast('')
        }
        /* 1url
          2{}
          3{emulateJSON: true}
        */
        this.$http
          .post('api/postcomment/' + this.id, {
            content: this.msg
          })
          .then(res => {
            if (res.body.status === 0) {
              this.comments.unshift({
                add_time: new Date(),
                content: this.msg,
                user_name: ''
              })
              this.msg = ''
            }
          })
      }
    },
    props: ['id']
  }
</script>
<style lang="scss">
  .cmt-container {
    h3 {
      font-size: 18 px;
      font-weight: 400;
      color: #f40;
    }

    textarea {
      font-size: 16px;
      height: 80px;
      margin-bottom: 0;
    }

    .cmt-list {
      margin: 5px 0;

      .cmt-item {
        font-size: 13px;

        .cmt-title {
          line-height: 30px;
          background: #ccc;
        }

        .cmt-body {
          line-height: 30px;
          text-indent: 2em;
          min-height: 30px;
        }
      }
    }
  }
</style>