
import Vue from 'vue'
//  vuex
import Vuex from 'vuex'
//  vuex
Vue.use(Vuex)
//  vuex
const store = new Vuex.Store({
  state: {
    car: JSON.parse(localStorage.getItem('car') || '[]') //
    // {id: id, count: , price: , selected: }
  },
  mutations: {
    // 
    addTocar(state, datacar) {
      let flag = false
      state.car.some(item => {
        if (item.id == datacar.id) {
          item.count += datacar.count
          item.selected = datacar.selected
          flag = true
          return
        }
      })
      if (!flag) {
        state.car.push(datacar)
      }
      localStorage.setItem('car', JSON.stringify(state.car))
    },
    // 

    updatedinfo(state, data) {
      state.car.forEach(item => {
        if (item.id == data.id) {
          item.count = data.count
          return
        }
      })
      // 
      localStorage.setItem('car', JSON.stringify(state.car))

    },
    // id 
    deldata(state, id) {
      state.car.forEach((item, index) => {
        if (item.id == id) {
          state.car.splice(index, 1)
          return
        }
      })
      localStorage.setItem('car', JSON.stringify(state.car))
    },
    // 
    selectedChange(state, data) {
      state.car.forEach(item => {
        if (parseInt(item.id) === parseInt(data.id)) {
          item.selected = data.selected
        }
      })
      localStorage.setItem('car', JSON.stringify(state.car))
    }
  },
  getters: {
    // 
    getAllcount(state) {
      let c = 0
      state.car.forEach(item => {
        c += item.count
      })
      return c
    },
    // id count
    getGoodsCount(state) {
      let count = {}
      state.car.forEach(item => {
        count[item.id] = item.count
      })
      return count
    },
    getselected(state) {
      let c = {}
      state.car.forEach(item => {
        c[item.id] = item.selected
      })
      return c
    },
    getgoodsmont(state) {
      let c = {
        count: 0, //
        amount: 0 //
      }
      state.car.forEach(item => {
        if (item.selected) {
          c.count += parseInt(item.count)
          c.amount = c.count * parseInt(item.price)
        }
      })
      return c
    }

  }
})
export default store